@import url("https://fonts.googleapis.com/css2?family=Saira:ital,wght@0,400;0,600;0,700;1,400&family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap");
:root {
  --white: #edf2f4;
  --strongPink: #ef233c;
  --brown: #8d99ae;
  --darkPink: #d90429;
  --backColor: #2b2d42;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--backColor);
  font-family: "Ubuntu", sans-serif;
}
.nav-bar {
  display: flex;
  justify-content: space-between;
  width: 90%;
  margin-top: 1em;
}
.logo {
  font-size: 2em;
  color: var(--strongPink);
  font-weight: lighter;
}
.Container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.time {
  color: var(--brown);
  font-family: "Saira", sans-serif;
  font-size: 1.5em;
}
.infoContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 90vh;
}
.spanContainer,
.countDownContainer {
  display: flex;
  justify-content: space-around;
  width: 500px;
}
.spanContainer,
.countDownContainer,
.countTime {
  margin-bottom: 2em;
}
.countDownContainer div {
  background-color: var(--strongPink);
  color: var(--white);
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2em;
  font-weight: bold;
}
.countDownContainer div {
  width: 90px;
  text-align: center;
}
.btn {
  width: 200px;
  background-color: var(--strongPink);
  color: var(--white);
  border: none;
  padding: 15px;
  font-weight: bold;
  border-radius: 8px;
  font-size: 1em;
}
.countTime {
  color: var(--brown);
  font-weight: bold;
}
.spanContainer {
  color: var(--brown);
  margin-bottom: 0;
}
.infoContainer h3 {
  color: var(--strongPink);
  font-size: 2em;
}
.fix {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.hide {
  display: none;
}
